@font-face {
    font-family: "GeeksNormal";
    src: url("../../../../assets/font/GEEKS\ Bet\ \(Normal\).ttf")
        format("truetype");
}

@font-face {
    font-family: "GeeksRoman";
    src: url("../../../../assets/font/GEEKS\ OzHandicraft\ Win95BT\ \(Roman\).ttf")
        format("truetype");
}

@font-face {
    font-family: "Mogul";
    src: url("../../../../assets/font/Mogul\ Futuris.ttf") format("truetype");
}

@font-face {
    font-family: "MogulBold";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulBoldItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulShadow";
    src: url("../../../../assets/font/Mogul\ FuturisShadow.ttf")
        format("truetype");
}

.footerContainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: rgb(44, 61, 81);
    color: white;
    padding: 2%;
}

.footerSecondContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 70%;
}

.footerDescription {
    width: 38%;
    min-width: 260px;
    font-size: 12px;
    text-align: end;
    margin-right: 4%;
    font-family: "Mogul";
}
.footerLogo {
    width: 20%;
    min-width: 100px;
    max-height: 130px;
    margin: 0 3%;
    font-family: "Mogul";
}

.ul > li {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}

.ul {
    width: 37%;
    list-style-type: none;
}

.footerHeaderIcon {
    fill: #82d900 !important;
    cursor: pointer !important;
    margin-right: 5px !important;
}

.footerLogoLinkContainer {
    display: flex;
    flex-direction: row;
    width: 58%;
}

@media only screen and (max-width: 1100px) {
    .footerDescription {
        width: 100%;
        text-align: center;
    }

    .footerSecondContainer {
        flex-direction: column;
    }
}

@media only screen and (max-width: 400px) {
    .footerSecondContainer {
        flex-direction: column;
    }
    .footerLogo {
        min-width: 50px;
        height: 95%;
        margin-top: 20%;
    }
}
