/* tablet and web */
@font-face {
    font-family: "GeeksNormal";
    src: url("../../../../assets/font/GEEKS\ Bet\ \(Normal\).ttf")
        format("truetype");
}

@font-face {
    font-family: "GeeksRoman";
    src: url("../../../../assets/font/GEEKS\ OzHandicraft\ Win95BT\ \(Roman\).ttf")
        format("truetype");
}

@font-face {
    font-family: "Mogul";
    src: url("../../../../assets/font/Mogul\ Futuris.ttf") format("truetype");
}

@font-face {
    font-family: "MogulBold";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulBoldItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulShadow";
    src: url("../../../../assets/font/Mogul\ FuturisShadow.ttf")
        format("truetype");
}

.headerContainer {
    background: white;
    height: 60px;
}

.headerIconContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.headerIcon {
    height: 90%;
    cursor: pointer;
}

.headerLinkContainer {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}

.headerLink {
    color: black !important;
    cursor: pointer !important;
    text-decoration: none !important;
    font-family: "Mogul" !important;
    font-size: 17px !important;
}

.headerIcon {
    fill: #82d900 !important;
    cursor: pointer !important;
    margin-right: 5px;
}

/* Mobile header */

.mobileHeaderLinkContainer {
    position: fixed;
    background-color: white;
}
